import Joi from 'joi'

const categoryValidator = {
    newCategoryValidator: Joi.object({
        category: Joi.string().pattern(new RegExp('^[\\s--\'-]+$')).min(3).max(20).required().messages({
            'string.pattern.base': '     .',
            'string.min': '     3   .',
            'string.max': '      20 .',
            'string.empty': '   \' '
        }),
    }),
    editCategoryValidator: Joi.object({
        category: Joi.string().pattern(new RegExp('^[\\s--\'-]+$')).min(3).max(20).optional().messages({
            'string.pattern.base': '     .',
            'string.min': '     3   .',
            'string.max': '      20 .'
        }),
    })
}

export {categoryValidator}