import Joi from 'joi'

const typeValidator = {
    newTypeValidator: Joi.object({
        type: Joi.string().pattern(new RegExp('^[\\s--\'-]+$')).min(3).max(20).required().messages({
            'string.pattern.base': '     .',
            'string.min': '     3   .',
            'string.max': '      20 .',
            'string.empty': '   \' ',
        }),
        _category: Joi.string().optional().messages({
            'string.empty': '   \' '
        })
    }),

    editTypeValidator: Joi.object({
        type: Joi.string().pattern(new RegExp('^[\\s--\'-]+$')).min(3).max(20).optional().messages({
            'string.pattern.base': '     .',
            'string.min': '     3   .',
            'string.max': '      20 .',
            'string.empty': '      !'
        }),
        _category: Joi.string().optional().messages({
            'string.empty': '      !'
        })
    })
}

export {typeValidator}