import Joi from 'joi'

const productValidator = {
    newProductValidator: Joi.object({
        title: Joi.string().pattern(new RegExp('^[\\s--\'-]+$')).min(3).max(20).required().messages({
            'string.pattern.base': '     .',
            'string.min': '     3   .',
            'string.max': '      20 .',
            'string.empty': '   \' !',
        }),
        category: Joi.string().min(5).messages({
            'string.min': '     5   .',
            'string.empty': '   \' !'
        }),
        type: Joi.string().min(5).optional(),
        info: Joi.string().max(500).required().messages({
            'string.empty': '      !',
            'string.max': '      200 .',
        }),
        price: Joi.number().integer().min(10).max(5000).required().messages({
            'number.min': '     10 .',
            'number.max': '     5000 .',
            'number.base': '   \' !'
        })
    }),
    editProductValidator: Joi.object({
        title: Joi.string().pattern(new RegExp('^[\\s--\'-]+$')).min(5).max(30).optional().messages({
            'string.pattern.base': '     .',
            'string.min': '     3   .',
            'string.max': '      20 .',
            'string.empty': '      !'
        }),
        info: Joi.string().max(500).optional().messages({
            'string.empty': '      !',
            'string.max': '      200 .',
        }),

        price: Joi.number().integer().min(10).max(5000).optional().messages({
            'number.min': '     10 .',
            'number.max': '     5000 .',
            'number.base': '     !'
        })
    })
}

export {productValidator}